// Light background
.ui.dimmer.modals.inverted {
	.ui.modal > .close,
	.ui.basic.modal > .close {
		color: var(--wp--preset--color--slate, black)!important;
	}
}
// Dark background
.ui.dimmer.modals:not(.inverted) {
	.ui.modal > .close,
	.ui.basic.modal > .close {
		color: var(--wp--preset--color--white, white)!important;
	}
}

// wp-admin pseudo modal
.wp-block-prc-block-popup-modal.wp-block.ui.modal {
    position: relative!important;
	z-index: inherit!important;
	margin-top: 1.5em!important;
	margin-bottom: 1.5em!important;
	&.active {
		position: fixed!important;
		z-index: 100!important;
		top: 16%;
		margin-left: 25%;
		left: 0;
	}
	&.basic {
		background: transparent!important;
	}
	.content {
		box-sizing: border-box;
	}
}
#prc-block-modal-background {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0,0,0,0.5);
	z-index: -1;
	&.active {
		z-index: 30;
	}
	&.inverted {
		background: rgba(255,255,255,0.75);
	}
}
